body {
  margin: 0;
  font-family: 'Kanit', Helvetica, sans-serif;
    
}


code {
  font-family: 'Kanit', Helvetica, sans-serif;
}

/* colors */
.color-blue{
  color: #1152B9;
}
.color-white{
  color: #fff;
}

.color-black{
  color: #070707
}

.color-grey-light{
  color: lightslategray
}

.color-lightBlue{
  color: rgba(17, 82, 185, 0.5);
}
.color-red{
  color: rgb(255, 0, 0);
}

/*backgroundColor */

.backgroundColor-white{
  background-color: white;
}
.backgroundColor-blue{
  background-color: #1152B9;
}
.backgroundColor-lightBlue{
  background-color: rgba(17, 82, 185, 0.5);
}
.backgroundColor-ligthBlack{
  background-color: rgb(7, 7, 7, 0.5);
}
.backgroundColor-black{
  background-color: rgb(7, 7, 7, 1);
}

/*border*/

.border-lightBlue{
  border: 1px rgba(17, 82, 185, 0.5) solid;
}
.border-blue{
  border: 6px rgba(17, 82, 185, 1) outset ;
}

/*display */

.flex{
  display: flex
}

.none{
  display: none;
}

/* Drop- Shadow*/
.drop-shadow-blue{
  filter: drop-shadow(2px 4px 4px #1152B9);
}
@media screen and (max-width:480px) {
  .drop-shadow-blue{
    filter: drop-shadow(4px 0px 11px #1152B9);
  }
}
.drop-shadow-white{
  filter: drop-shadow(2px 4px 4px #fff);
}
.drop-shadow-black{
  filter: drop-shadow(2px 4px 4px #070707);
}

/* Animation */

.animation-appearFromDown{
  animation: appearFromDown 1500ms cubic-bezier(.42,1.6,.71,.33) both ;
}
.animation-dissappearFromDown{
  animation: dissappearFromDown 1500ms cubic-bezier(.29,.84,.34,-0.27) both ;
}
.animation-appearFromUP{
  animation: appearFromUP 1000ms ease-in-out both ;
}
.animation-dissappearFromUP{
  animation: dissappearFromUP 1000ms ease-in-out both ;
}
.animation-appearFromLeft{
  animation: appearFromLeft 1000ms cubic-bezier(0.175, 0.885, 0.32, 1.275) both ;
}
.animation-dissappearFromLeft{
  animation: dissappearFromLeft 1000ms cubic-bezier(1,.05,.75,1.02) both ;
}
.animation-appearFromRight{
  animation: appearFromRight 1100ms cubic-bezier(.7,.22,.57,1.37) both;
}
.animation-dissappearFromRight{
  animation: dissappearFromRight 1000ms cubic-bezier(1,.05,.75,1.02) both;
}
.delay-0{
  animation-delay: 0ms;
}
.delay-1{
  animation-delay: 100ms;
}
.delay-2{
  animation-delay: 200ms;
}
.delay-3{
  animation-delay: 300ms;
}
.delay-4{
  animation-delay: 400ms;
}
.delay-5{
  animation-delay: 500ms;
}
.delay-5{
  animation-delay: 600ms;
}
.delay-6{
  animation-delay: 700ms;
}
.delay-8{
  animation-delay: 800ms;
}

.delay-9{
  animation-delay: 900ms;
}
.delay-10{
  animation-delay: 1000ms;
}

/* Keyframes */
@keyframes appearFromUP {
  0%{
    transform: translateY(-50px);
    opacity: 0;
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes dissappearFromUP {
  0%{
    opacity: 1;
    transform: translateY(0px);
  }
  100%{
    opacity: 0;
    transform: translateY(-10px);
  }
}
@keyframes appearFromRight {
  0%{
    transform: translate(10px);
    opacity: 0;
  }
  100%{
    opacity: 1;
    transform: translate(0px);
  }
}
@keyframes dissappearFromRight {
  0%{
   transform: translate(0px);
   opacity: 1;
  }
  100%{
    opacity: 0;
    transform: translate(10px);
  }
}
@keyframes appearFromLeft {
  0%{
   transform: translateX(-50px);
   opacity: 0;
  }
  100%{
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes dissappearFromLeft {
  0%{
   transform: translateX(0px);
   opacity: 1;
  }
  100%{
    opacity: 0;
    transform: translateX(-50px);
  }
}
@keyframes appearFromDown {
  0%{
   transform: translateY(30px);
   opacity: 0;
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes dissappearFromDown {
  0%{
   transform: translateY(0px);
   opacity: 1;
  }
  100%{
    opacity: 0;
    transform: translateY(30px);
  }
}

