@keyframes Spinner {
  0% { opacity: 1 }
  100% { opacity: 0 }
}
.Spinner div {
  left: 96.255px;
  top: 42.434999999999995px;
  position: absolute;
  animation: Spinner linear 0.8928571428571428s infinite;
  background: #93dbe9;
  width: 14.489999999999998px;
  height: 31.049999999999997px;
  border-radius: 7.244999999999999px / 10.557px;
  transform-origin: 7.244999999999999px 61.065px;
}
.Spinner div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -0.8184523809523808s;
  background: #93dbe9;
}
.Spinner div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -0.744047619047619s;
  background: #93dbe9;
}
.Spinner div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.6696428571428571s;
  background: #93dbe9;
}
.Spinner div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.5952380952380952s;
  background: #93dbe9;
}
.Spinner div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.5208333333333333s;
  background: #93dbe9;
}
.Spinner div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.4464285714285714s;
  background: #93dbe9;
}
.Spinner div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.3720238095238095s;
  background: #93dbe9;
}
.Spinner div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.2976190476190476s;
  background: #93dbe9;
}
.Spinner div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.2232142857142857s;
  background: #93dbe9;
}.Spinner div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.1488095238095238s;
  background: #93dbe9;
}.Spinner div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.0744047619047619s;
  background: #93dbe9;
}
.Spinner div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
  background: #93dbe9;
}
.Spinner-Container {
  width: 24px;
  height: 24px;
  display: inline-block;
  overflow: hidden;
}
.Spinner {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0; /* see note above */
}
.Spinner div { 
    box-sizing: content-box;
}